:root {
  --primary-limegreen: hsl(163, 72%, 41%);
  --primary-brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-gradient-start: hsl(37, 97%, 70%);
  --instagram-gradient-middle: hsl(5, 77%, 71%);
  --instagram-gradient-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-gradient-start: hsl(210, 78%, 56%);
  --toggle-gradient-end: hsl(146, 68%, 55%);
  --toggle-bg-lt: hsl(230, 19%, 60%);
  --top-bg-dk: hsl(232, 19%, 15%);
  --card-bg-dk: hsl(228, 28%, 20%);
  --txt-dk: hsl(228, 34%, 66%);
  --top-bg-lt: hsl(225, 100%, 98%);
  --card-bg-lt: hsl(227, 47%, 96%);
  --txt-lt: hsl(228, 12%, 44%);
  --dark-bg-lthm-txt: hsl(230, 17%, 14%);
  --light-bg-dkthm-txt: hsl(0, 0%, 100%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --light-card-hover: hsl(228, 33%, 91%);
  --background: var(--light-bg-dkthm-txt);
  --color: var(--dark-bg-lthm-txt);
  --card-bg: var(--card-bg-lt);
  --txt-theme: var(--dark-bg-lthm-txt);
  --txt-color: var(--txt-lt);
  --toggle-txt-color: var(--toggle-bg-lt);
  --toggle-bkgrnd: var(--toggle-bg-lt);
  --toggle-btn: var(--light-bg-dkthm-txt);
  --card-hover: var(--light-card-hover);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg-lthm-txt);
    --color: var(--light-bg-dkthm-txt);
    --card-bg: var(--card-bg-dk);
    --txt-theme: var(--light-bg-dkthm-txt);
    --txt-color: var(--txt-dk);
    --toggle-txt-color: var(--light-bg-dkthm-txt);
    --toggle-bkgrnd: linear-gradient(225deg, var(--toggle-gradient-end) 0%, var(--toggle-gradient-start) 98.02%);
    --toggle-btn: var(--dark-bg-lthm-txt);
    --card-hover: var(--dark-card-hover);
  }
}
body.dark {
  --background: var(--dark-bg-lthm-txt);
  --color: var(--light-bg-dkthm-txt);
  --card-bg: var(--card-bg-dk);
  --txt-theme: var(--light-bg-dkthm-txt);
  --txt-color: var(--txt-dk);
  --toggle-txt-color: var(--light-bg-dkthm-txt);
  --toggle-bkgrnd: linear-gradient(225deg, var(--toggle-gradient-end) 0%, var(--toggle-gradient-start) 98.02%);
  --toggle-btn: var(--dark-bg-lthm-txt);
  --card-hover: var(--dark-card-hover);
}

body.light {
  --background: var(--light-bg-dkthm-txt);
  --color: var(--dark-bg-lthm-txt);
  --card-bg: var(--card-bg-lt);
  --txt-theme: var(--dark-bg-lthm-txt);
  --txt-color: var(--txt-lt);
  --toggle-txt-color: var(--toggle-bg-lt);
  --toggle-bkgrnd: var(--toggle-bg-lt);
  --toggle-btn: var(--light-bg-dkthm-txt);
  --card-hover: var(--light-card-hover);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--txt-color);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  gap: 0.3rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle legend {
  font-size: 0.875rem;
  font-weight: 700;
  grid-column: 1/2;
  float: left;
  align-self: center;
  color: var(--toggle-txt-color);
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggle-bkgrnd);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-btn);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 26px);
  right: 3px;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--card-bg);
  color: var(--txt-theme);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 0.25rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.25rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  height: 0.25rem;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-gradient-end), var(--instagram-gradient-middle) 50.91%, var(--instagram-gradient-start) 100%);
}
.card--youtube {
  border-top: 0.25rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--txt-theme);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
  font-weight: 700;
}
.card__label {
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  color: var(--txt-color);
  font-weight: 400;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--primary-limegreen);
}
.card__change--down {
  color: var(--primary-brightred);
}
.card__change img {
  margin-right: 0.25rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}